<template>
    <div>
      <h2>Editar</h2>
      <form @submit.prevent="actualizarCliente">
              <div>
                  <input
                      type='number'
                      name='cedula'
                      class='form-control'
                      placeholder='Cedula'
                      autoComplete='off'
                      v-model="editCliente.cedula"
                  />
                  <input
                      type='text'
                      name='nombre'
                      class='form-control mt-3'
                      placeholder='Nombre'
                      autoComplete='off'
                      v-model="editCliente.nombre"
                  />
                  <input
                      type='text'
                      name='apellidos'
                      class='form-control mt-3'
                      placeholder='Apellidos'
                      autoComplete='off'
                      v-model="editCliente.apellidos"
  
                  />
                   <input
                      type='number'
                      name='celular'
                      class='form-control mt-3'
                      placeholder='Celular'
                      autoComplete='off'
                      v-model="editCliente.celular"
  
                  />
                  <input
                      type='text'
                      name='direccion'
                      class='form-control mt-3'
                      placeholder='Direccion'
                      autoComplete='off'
                      v-model="editCliente.direccion"
         
                  />
  
                  <button 
                  class='btn btn-primary form-control  mt-2'
                  type='submit'  
                  @click="actualizarCliente"
                  >
                  Actualizar
  
                  </button>
              </div>
          </form>
      <button class="btn btn-success mt-5" @click="setEdit" >Salir</button>
    </div>
  </template>
  
  <script>
  import { mapActions, mapMutations, mapState } from 'vuex'
  
  export default {
      name: 'EditSocio',
      data(){
          return{
              editCliente:{},
              
          }
      },
      mounted(){
          
          this.editCliente = this.cliente
      },
      computed: {
          ...mapState('cliente',['cliente']),
      },
      methods:{
              ...mapMutations('cliente',['setEdit', 'closeEdit']),
              ...mapActions('cliente',['updateCliente'])
          ,
          async actualizarCliente(){
                         
              await this.updateCliente(this.editCliente)
              this.setEdit()
              this.closeEdit()
          }
      },
      unmounted(){
        this.closeEdit()
      }
  }
  </script>
  
